*{
  box-sizing:border-box;
}

.Project{
 color:#FFFFFF;
  display: flex;
  overflow-x:scroll;
  padding:24px;
  scroll-snap-type:x mandatory;
  border-radius:8px;
  gap:12px;
  justify-items: center;
  
}

 .item{
  flex: 0 0 30%;
  padding: 24px;
  border-radius: 8px;
  scroll-snap-type: start;
  border:3px solid white;



}
