.gitStats{
    display:grid;
    grid-template-columns:repeat(3,1fr);
     justify-content:space-between;
      gap:10;
      margin-top: 100px ;
     
}



.box3 img{
height: 200px;


}

@media screen  and (max-width:1024px){
   .gitStats{
    grid-template-columns:repeat(1,1fr);
    text-align: center;
    /* margin-left: 120px; */
    
   }
   .gitStats div img {
    margin-top: 20px;
    width: 100%;
   }
   .box3 img{
    /* height: 200px; */
    width: 100%;
    
    }
}

@media screen  and (max-width:500px){
    .gitStats{
        grid-template-columns:repeat(1,1fr);
       }
}